	onLoad(options) {
		// this.token = uni.getStorageSync('token')
		this.getToken()
	},
	methods: {
		// 获取Token
		getToken() {
			uni.login({
				success: res => {
					if (res.code) {
						console.log(res.code);
						uni.request({
							url: `${helper.requestUrl}/refresh`,
							method: 'POST',
							header: {
								authorization: this.$options.globalData.token
							},
							success: data => {
								data = helper.null2str(data);
								console.log(data);
								if (data.statusCode == '200') {
									let token = `${data.data.token}`;
									this.$options.globalData.token = token;
									uni.setStorageSync('token',this.$options.globalData.token)
									console.log(this.$options.globalData.token);
								}
							}
						});
					}
				}
			});
		}
	}
